// Ensure modules (and their wp-hooks) load before the App initializes the router
// Bootstrap app with controlled load order so wp-hooks (filters) register
// only if the corresponding module is enabled, and before router creation.
(async () => {
    try {
        // 1) Load dynamic modules based on window.wpcafe_enabled_modules
        await import("./modules");

        // 2) Load store (order not critical, but keep before App for consistency)
        await import("./store");

        // 3) Load App (which imports the router). By this time, any module hooks
        //    like food-ordering/addRouter will be registered if the module is enabled.
        const { default: App } = await import("./App");

        const rootEl = ReactDOM.createRoot(
            document.getElementById("wpcafe_dashboard"),
        );
        rootEl.render(<App />);
    } catch (err) {
        console.error("Failed to bootstrap WPCafe admin app:", err);
    }
})();
